import { Component } from "@angular/core";
import { BaseComponent } from "src/app/components/base.component";
import { commonConverterViewModel } from "src/app/models/view/common-converter.viewmodel";
import { CommonService } from "src/app/services/common.service";
import { LogHandlerService } from "src/app/services/log-handler.service";

@Component({
  selector: "app-temperature",
  templateUrl: "./temperature.component.html",
  styleUrls: ["./temperature.component.scss"],
})
export class TemperatureComponent extends BaseComponent<commonConverterViewModel> {
  override viewModel: commonConverterViewModel = new commonConverterViewModel();
  constructor(commonService: CommonService, logService: LogHandlerService) {
    super(commonService, logService);
  }
  temperatureUnitNames = ["Celsius (C)", "Fahrenheit (F)", "Kelvin (K)"];

  targetUnit: string = this.temperatureUnitNames[0];

  // function to convert weight
  convertTemp() {
    if (this.viewModel.temperature != null) {
      if (this.targetUnit === "Celsius (C)") {
        this.fromCelsius();
      } else if (this.targetUnit === "Fahrenheit (F)") {
        this.fromFahrenheit();
      } else if (this.targetUnit === "Kelvin (K)") {
        this.fromKelvin();
      } else {
        this.resetValues();
      }
    } else {
      this.resetValues();
    }
  }
  fromCelsius() {
    if (this.viewModel.temperature != null) {
      this.viewModel.celsius = this.viewModel.temperature;
      this.viewModel.fahrenheit = +((this.viewModel.temperature * 9) / 5 + 32);
      this.viewModel.kelvin = +(this.viewModel.temperature + 273.15);
    }
  }

  fromFahrenheit() {
    if (this.viewModel.temperature != null) {
      this.viewModel.fahrenheit = this.viewModel.temperature;
      this.viewModel.celsius = +(((this.viewModel.temperature - 32) * 5) / 9);
      this.viewModel.kelvin = +(
        ((this.viewModel.temperature - 32) * 5) / 9 +
        273.15
      );
    }
  }

  fromKelvin() {
    if (this.viewModel.temperature != null) {
      this.viewModel.kelvin = this.viewModel.temperature;
      this.viewModel.celsius = +(this.viewModel.temperature - 273.15);
      this.viewModel.fahrenheit = +(
        ((this.viewModel.temperature - 273.15) * 9) / 5 +
        32
      );
    }
  }

  resetValues() {
    this.viewModel.temperature = null;
    this.viewModel.celsius = null;
    this.viewModel.kelvin = null;
    this.viewModel.fahrenheit = null;
  }
}
